.Header {
    width: 90%;
    display: inline-flex;
    align-self: flex-end;
    border-bottom: 2px solid black;
    justify-content: end;

    &_item {
        border-left: 2px solid black;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        border: 2px solid black;
    }
}


.LeftMenu {
    height: 100%;
    width: 10%;
    border-right: 2px solid black;
    position: absolute;
    padding-top: 2.4rem;

    &_item {
        padding: 1rem 0 1rem 2rem;
    }
}

.Main_container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    height: 100%;
}

.Page {
    flex-direction: column;
    margin-left: 10rem;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    >.Face {
        position: relative;
        height:20%;
        width: 20%;

        > .RightEye {
            
            position: absolute;
            top: 0px;
            right: 0px;
        }

        > .LeftEye {
            
            position: absolute;
            top:0;
            left: 0px;
        }

        > .Nose {
            position: absolute;
            top:120px;
            left: 105px;  
        }

    }
}
.Smile {
	width: 250px;
	height: 70px;
	border: 40px solid black;
	border-top: 0;
	background: rgba(0,0,0,0);
	border-radius: 0 0 120px 120px / 0 0 90px 90px;
	position: absolute;
	bottom: -100px;
	left: -36px;
	}
.Square {

    background-color: black;
    width: 50px;
    height: 50px;
}